
import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'



import { getItem, deleteItems, getItems } from '../../api/inventory/itemApi'
import DEMO from '../../globals/constant';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getItemCategory, deleteItemCategory } from '../../api/inventory/itemCategory';
import isEmpty from '../../validation/isEmpty';
import IsEmpty from '../../App/components/Reusables/IsEmpty'
import ItemsTable from '../Items/ItemsTable';

let path = []
const createPath = async (parent_tree) => {
    path.push(parent_tree.details[0])
    if (!isEmpty(parent_tree.sub_categories)) {
        createPath(parent_tree.sub_categories)
    }
    return;
}

const ItemCategory = (props) => {


    const [itemCategory, setItemCategory] = React.useState(null);
    const [loading, setLoading] = React.useState(true);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, itemCategoryId: null, itemsLength: null })
    const [parentPath, setParentPath] = React.useState([]);
    const [subCats, setSubCats] = React.useState([])
    const [itemsArr, setItemsArr] = React.useState([])





    React.useEffect(() => {


        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            await getItemCategory({ action: 'get', item_category_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        setItemCategory(res.json.item_category[0])
                        createPath(res.json.parent_tree)
                        await setParentPath(path);
                        path = []
                        setSubCats(res.json.sub_categories)
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err);
                });

            //Get items of this category
            await getItems({ "action": "get", "filter": "category", category_id: props.match.params.id, "start": 0, "end": 50 }, controller.signal)
                .then((res) => {
                    console.log(res)
                    if (res.json.status) {
                        setItemsArr(res.json.items)
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });

            if (ladder === 2) {
                setLoading(false)
            }
        })()
        return () => controller.abort();
    }, []);

    const _triggerDeleteModal = () => {
        setDeleteData({ showModal: true, itemCategoryId: itemCategory.id, itemsLength: itemCategory.count_item });
    }

    const _delete = (toBeDeletedId) => {
        deleteItemCategory({ action: 'delete', item_categories_id: [toBeDeletedId] })
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    setDeleteData({ showModal: false, itemCategoryId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Item Category deleted successfully!', background: '#dffff0' })
                    props.history.push('/item-categories')
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err);
            });
    }

    console.log('==========================================');
    console.log(parentPath);
    console.log('==========================================');


    if (loading) return <Loader />
    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" style={{ lineHeight: '33px' }} >{itemCategory.name}</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/item-category/update/` + itemCategory.id)}
                    > <i className="feather icon-edit text-c-white f-15 m-r-5" />Update</button>
                    <button
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={_triggerDeleteModal}
                    > <i className="feather icon-trash-2 text-c-white f-15 m-r-5" />Delete</button>
                </div>
            </Row>
            <Row>
                <Col xl={1}></Col>
                <Col xl={10}>
                    <Card>
                        <Card.Body>
                            <h5 className='mb-4'>Item Category Details</h5>
                            <div style={{ position: 'relative', top: '-20px' }}>

                                {/* --------------------catcrumb---------------- */}
                                <div style={{ margin: '4px 3px 4px 3px', color: '#42768a' }}>
                                    {parentPath.map((cat, index) => {
                                        if (index === parentPath.length - 1) {
                                            return <span style={{ display: 'inline', cursor: 'pointer' }} key={index}>{cat.name}</span>;
                                        } else {
                                            return (
                                                <div
                                                    key={index}
                                                    style={{ display: 'inline', cursor: 'pointer' }}
                                                    onClick={() => { props.history.push('/item-category/' + cat.id) }}
                                                >
                                                    {cat.name}
                                                    < i className="feather icon-chevron-right text-c-white f-15 " />
                                                </div>
                                            )
                                        }
                                    })}
                                </div>
                                {/* --------------------catcrumb---------------- */}

                            </div>
                            <br />
                            <div className="row d-flex align-items-center">

                                <Col md={2}></Col>
                                <Col md={8}>

                                    <div className='mb-1 border-bottom'><b>Category Name</b></div>
                                    <div className='mt-1 mb-3'>
                                        {itemCategory.name} {" "}
                                        {itemCategory.is_active ? (<Badge variant="info">Active</Badge>) : (<Badge variant="secondary">Inactive</Badge>)}
                                    </div>

                                    {subCats.length > 0 ?
                                        (
                                            <>
                                                <div className='mb-1 border-bottom'><b>Sub-Categories</b></div>
                                                {subCats.map((cat, index) => (
                                                    <li
                                                        key={index}
                                                        style={{ color: '#42768a', cursor: 'pointer' }}
                                                        className='mt-1 mb-0'
                                                        onClick={() => props.history.push('/item-category/' + cat.id)}
                                                    >
                                                        {cat.name}
                                                    </li>
                                                ))}

                                            </>) :
                                        null}

                                </Col>
                                <Col md={2}></Col>

                            </div>
                            <br />

                            {itemsArr.length < 1 ?
                                (<IsEmpty>There are no items of {itemCategory.name} Category!</IsEmpty>) :
                                (
                                    <ItemsTable
                                        itemCategoryId={props.match.params.id}
                                        tableHeader={`Items of ${itemCategory.name} Category`}
                                    />
                                )}


                        </Card.Body>
                    </Card>
                </Col>
                <Col xl={1}></Col>


                <DeleteModal
                    show={deleteData.showModal}
                    onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                    onDelete={() => _delete(deleteData.itemCategoryId)}
                    message={
                        deleteData.itemsLength > 0 ?
                            'First, delete the items associated to this category to delete this cateogry.' :
                            null
                    }
                    noRedButton={deleteData.itemsLength > 0}
                />

            </Row>
        </>
    );
}

export default ItemCategory;
